import React, { useState } from "react";
import { useParams } from "react-router-dom";
import useHttp from "../../Shared/hooks/http-hook";
import PlaceList from "../Components/PlaceList";
import { useEffect } from "react";
import ErrorModal from "../../Shared/Components/Utility/ErrorModal";
import LoadingSpinner from "../../Shared/Components/Utility/LoadingSpinner";

const Users = () => {
  let userId = useParams().uid;
  const [loadedPlaces, setLoadedPlaces] = useState();
  const { error, isLoading, sendRequest, onCancelHandler } = useHttp();

  useEffect(() => {
    const fetchUser = async () => {
      try {
        const responseData = await sendRequest(process.env.REACT_APP_BACKEND_ADDRESS+
          `/places/users/${userId}`
        );
        setLoadedPlaces(responseData.places);
      } catch (err) {}
    };
    fetchUser();
  }, [sendRequest, userId]);
  // const Dummy_Places = [
  //   {
  //     id: "p1",
  //     image:
  //       "https://images.unsplash.com/photo-1541537103745-ea3429c65dc4?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=387&q=80",

  //     title: "Times Square",
  //     address: "Manhattan, NY 10036, United States",
  //     location: {
  //       lat: 40.7537477,
  //       lng: -73.9956965,
  //     },
  //     creator: "u2",
  //     description: "Place in london",
  //   },
  //   {
  //     id: "p2",
  //     image:
  //       "https://images.unsplash.com/photo-1541537103745-ea3429c65dc4?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=387&q=80",

  //     title: "Times Square",
  //     address: "Manhattan, NY 10036, United States",
  //     location: {
  //       lat: 40.7537477,
  //       lng: -73.9956965,
  //     },
  //     creator: "u1",
  //     description: "Place in london",
  //   },
  // ];

  // let places = Dummy_Places.filter((places) => places.creator === userId);
  const onDeleteHandeler = (pID) => {
    setLoadedPlaces((previousPlaces) =>
      previousPlaces.filter((place) => place.id !== pID)
    );
  };

  return (
    <React.Fragment>
      <ErrorModal error={error} onClear={onCancelHandler}></ErrorModal>
      {isLoading && <LoadingSpinner asOverlay></LoadingSpinner>}
      {!error && !isLoading && loadedPlaces && (
        <PlaceList
          onDelete={onDeleteHandeler}
          items={loadedPlaces}
        ></PlaceList>
      )}
    </React.Fragment>
  );
};

export default Users;
